<template>
  <div>
      <header class="headerBox">
          <div class="width110">
              <div class="header_left">
                  <router-link :to="{name:'index'}">晓卫博客 <span class="xin">|</span> <span class="xinH2">心向往之，行必能至</span></router-link>
              </div>
            <div class="header_right">
                <ul>
                    <li><router-link :to="{name:'blogList'}">首页</router-link></li>
                    <!-- <li><router-link to="/index">前端知识</router-link></li>
                    <li><router-link to="/index">后端知识</router-link></li> -->
                    <li><router-link :to="{name:'archive'}">文章归档</router-link></li>
                    <li><router-link :to="{name:'message'}">留言板</router-link></li>
                    <li><router-link :to="{name:'author'}">关于作者</router-link></li>
                    <li><router-link :to="{name:'link'}">友情链接</router-link></li>
                </ul>
            </div>
          </div> 
          <div class="sideOn" @click="drawer = true" type="primary" style="margin-left: 16px;"><i class="el-icon-s-unfold"></i></div>
      </header>
      <div class="contentBox">
          <div class="content">
            <el-row :gutter="10">
                <el-col :xs="24" :sm="24" :md="16" :lg="16" :xl="16" class="content_left">
                    <!-- <div></div> -->
                    <!-- <blog-list></blog-list> -->
                    <router-view :key="key"></router-view>
                </el-col>
                <el-col class="content_right" :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
                    <div class="content_right_Box">
                        <h2 class="htitle">标签云</h2>
                        <!-- <ul class="rightBoxUl">
                            <li v-for="(item,index) in 6" :key="index"><router-link tag="a" to="/index">标题哈哈标题哈哈标题哈哈标题哈哈标题哈哈标题哈哈标题哈哈标题哈哈标题哈哈标题哈哈</router-link></li>
                        </ul> -->
                        <div class="changeColorBox">
                            <!-- <span :style="color:randomColor(); fontSize:randomSize()">jave</span> -->
                            <!-- <span :style="{color:spanColor, fontSize:spanSize}">前端</span> -->
                            <span :style="{background:randomColor()}" v-for="(item,index) in tagsList" :key="index"><router-link :to="{name:'tagBlogPage',query:{tagId:item._id}}">{{item.tag}}</router-link></span>
                        </div>
                    </div>
                    <div class="content_right_Box">
                        <h2 class="htitle">点击排行</h2>
                        <ul class="rightBoxUl">
                            <li v-for="(item,index) in sortBlogList" :key="index"><router-link tag="a" :to="{path:'/blogDetail',query:{blogId:item._id}}">{{item.title}}</router-link></li>
                        </ul>
                    </div>
                    <div class="content_right_Box">
                        <h2 class="htitle">本站推荐</h2>
                        <ul class="rightBoxUl">
                            <li v-for="(item,index) in 6" :key="index"><router-link tag="a" to="/index">标题哈哈</router-link></li>
                        </ul>
                    </div>
                    <div class="content_right_Box">
                        <h2 class="htitle">友情链接</h2>
                        <ul class="rightBoxUl">
                            <li v-for="(item,index) in linkArr" :key="index"><a :href="item.wenLink" target="_blank" :title="item.webName">{{item.webName}}</a></li>
                        </ul>
                    </div>
                </el-col>
            </el-row>
        </div>
      </div>
      <!-- //底部 -->
      <footer class="footerBox">
          <p>Copyright © Kwin 2017 - 2019 | Design by kwin.wang | 本站总访问量12642次</p>
          <p>ICP证： 浙ICP备18023109号-1</p>
      </footer>
      <!-- //抽屉 -->
      <el-drawer
            class="sideBox"
        title="博客导航"
        size='50%'
        :visible.sync="drawer"
        :direction="direction">
        <ul>
                            <li><router-link to="/index">首页</router-link></li>
                            <li><router-link to="/index">首页</router-link></li>
                            <li><router-link to="/index">留言板</router-link></li>
                            <li><router-link to="/index">归档</router-link></li>
                            <li><router-link to="/index">友链</router-link></li>
                        </ul>
        </el-drawer>
  </div>
</template>

<script>
// import blogList from "./blogList";
import axios from "../axios"
export default {
    components:{
        // blogList
    },
    data(){
        return{
            drawer: false,
            direction: 'ltr',
            sortBlogList:[],
            tagsList:[],
            linkArr:[],
            
        }
    },
    created(){
       this.viewsSort();
       this.getQueryAllTags();
       this.getLink();
    },
    methods:{
        
        randomColor(){
                var red = Math.random() * 255;
                var green = Math.random() * 255;
                var blue = Math.random() * 255;
                return "rgb(" + red + ", " + green + ", " + blue + ")";
        },
        randomSize() {
                var size = (Math.random() * 14 + 10) + "px";
                return size;
        },
        //浏览量倒序查询
        viewsSort(){
            axios.get('/api/web/viewsSort')
                .then(res=>{
                    console.log('数量排序：',res)
                    this.sortBlogList = res.data;
                }).catch(err=>{
                    console.log(err)
                })
        },
        //获取友情链接
        getLink(){
            axios.get('/api/web/queryAllLinkPass').then(res=>{
                console.log(res);
                this.linkArr=res.data.rows
                
            }).catch(err=>{
                console.log(err);
            })
        },
        //tag查询
        getTagsFind(){
            axios.post('/api/web/tagsFind',{
                tags:'JavaScript'
            }).then(res=>{
                console.log(res);
                
            }).catch(err=>{
                console.log(err);
            })
        },
        // 获取标签列表
        getQueryAllTags(){
            axios.get('/api/web/queryAllTags')
            .then(res=>{
                console.log(res);
                this.tagsList=res.data;
            }).catch(err=>{
                console.log(err);
            })
        }
        
    },
    computed:{
        key(){
            return this.$route.name?this.$route.name+ +new Date():this.$route+ +new Date()
        }
    }
}
</script>

