<template>
    <div class="linkBox">
        <h2 class="htitle">申请友情链接</h2>
        <el-form ref="form" :model="form" label-width="80px" style="padding-top:20px">
            <el-form-item label="昵称" class="opk">
                <el-input v-model="form.name" placeholder="请填写昵称"></el-input>
            </el-form-item>
            <el-form-item label="邮箱" class="opk">
                <el-input v-model="form.email" placeholder="请填写邮箱"></el-input>
            </el-form-item>
            <el-form-item label="网站名" class="opk">
                <el-input v-model="form.webName" placeholder="请填写网站名称"></el-input>
            </el-form-item>
            <el-form-item label="网站链接" class="opk">
                <el-input v-model="form.link" placeholder="请填写网站链接"></el-input>
            </el-form-item>
            <el-form-item label="外链类型">
                <el-select v-model="form.webType" placeholder="请选链接类型">
                <el-option label="个人博客" value="个人博客"></el-option>
                <el-option label="常用网站" value="常用网站"></el-option>
                <el-option label="微服务" value="微服务"></el-option>
                </el-select>
            </el-form-item>
            
            <!-- <el-form-item label="即时配送">
                <el-switch v-model="form.delivery"></el-switch>
            </el-form-item> -->
            <el-form-item label="备注信息">
                <el-input type="textarea"  placeholder="请填写备注信息" v-model="form.webDesc"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="addLink">提交申请</el-button>
                <!-- <el-button>取消</el-button> -->
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import axios from "../axios";
export default {
    data() {
      return {
        form: {
          name: '',
          email: '',
          webName: '',
          link: '',
          webType: [],
          webDesc: ''
        }
      }
    },
    methods:{
        
        addLink(){
            console.log(this.form)
            axios
        .post("/api/web/addLink", {
          nickname: this.form.name,
          email: this.form.email,
          webName: this.form.webName,
          wenLink: this.form.link,
          linkType: this.form.webType,
          remark: this.form.webDesc,

        })
        .then(res => {
          console.log("获取到信息了吗", res);
          if(res.data.status===1){
              this.form={
                name: '',
                email: '',
                webName: '',
                link: '',
                webType: [],
                webDesc: ''
              };
              this.$message({
                message: '提交成功，请等待通知',
                type: 'success'
                });
          }
        })
        .catch(err => {
          console.log(err);
        });
      console.log(this.form);
        }
    },
}
</script>

<style scoped>
.linkBox{ background: #fff; border-radius: 5px; padding: 20px;}
    .htitle { font-size: 16px; line-height: 40px; border-bottom: 1px solid #eee; color: #484848; font-weight: normal; position: relative; margin-bottom: 10px; }
    .htitle:after { content: ""; position: absolute; width: 60px; height: 2px; content: ""; background: #000; left: 0; bottom: 0; -moz-transition: all .5s ease; -webkit-transition: all .5s ease; transition: all .5s ease; }
    .htitle:hover:after { width: 80px; }
</style>