<template>
  <div class="blogDetailPage">
      <div class="blogDetailPageContent" v-loading="loading" element-loading-text="文章加载中">
          <h2 class="htitle"><span class="con_nav">您现在的位置是：<router-link to="/index">网站首页</router-link>&gt;<a href="/info/23.html">文章详情</a></span>文章详情</h2>
        <h1 class="con_tilte">{{blogDetailData.title}}</h1>
        <p class="bloginfo"><i class="el-icon-user"></i><span>{{blogDetailData.author}}</span>  <i class="el-icon-alarm-clock"></i><span v-if="blogDetailData.ctime">{{blogDetailData.ctime.split('T')[0]}}</span>
                <span class="viewnum f_r"><i class="el-icon-view"></i>浏览({{blogDetailData.views}})</span>
                <span class="pingl f_r"><i class="el-icon-chat-dot-square"></i>评论(0)</span>
                <span class="likel f_l"><i class="el-icon-view"></i>标签：{{blogDetailData.tags}}</span>
        </p>
        <!-- <div v-html="blogDetailData.content">
            
        </div> -->
        <div class="ql-container ql-snow">

            <div class="ql-editor">

            <div v-html="blogDetailData.content"></div>

            </div>

        </div>
      </div>
      <div style="margin-top:10px">
            <Comments :myblogId='blogId'></Comments>
        </div>
      
  </div>
</template>

<script>
import 'quill/dist/quill.core.css';
    import 'quill/dist/quill.snow.css';
    import 'quill/dist/quill.bubble.css';
import axios from '../axios';
import Comments from './comments'
export default {
    data(){
        return{
            blogId:'',
            blogDetailData:'',
            loading:true,
        }
    },
    components:{
        Comments,
    },
    created(){
        this.blogId = this.$route.query.blogId;
        // console.log('传过来的值：',this.$route.query,this.$route)
        // alert('值改变了')
        this.addViewsNum();
    },
    methods:{
        //获取博客详情
        getBlogDetailData(){
            axios.get('/api/web/queryOneBlog',{
                    params:{
                        id:this.blogId
                    }
                })
                .then(res=>{
                    console.log('博客详情的数据：',res)
                    if(res.status==200){
                        this.blogDetailData = res.data[0]
                        this.loading=false;
                    }
                })
        },
        //增加浏览量
        addViewsNum(){
            axios.post('/api/web/viewsNum',{
                id:this.blogId
            })
            .then(res=>{
                console.log(res)
                this.getBlogDetailData();
            }).catch(err=>{
                console.log(err)
            })
        }
    }

}
</script>

