<template>
  <div>
      <el-carousel :interval="5000" arrow="always" height="200px" class="bannerIndex" v-if="!tagNameInfo">
                        <el-carousel-item v-for="item in bannerImgList" :key="item._id">
                        <!-- <h3>{{ item }}</h3> -->
                        <router-link :to="{path:'/blogDetail',query:{blogId:item.region.split(',')[1]}}"><img :src="item.bannerImg" alt=""></router-link>
                        </el-carousel-item>
                    </el-carousel>
                    <div v-loading="loading" style="min-height: 100px; ">
                        <div v-if="isData">
                            <div class="articleBox" v-for="(item,index) in blogListData" :key="index" >
                                <!-- <div class="articleBox_title">
                                    <div class="">
                                        <h3>文章标题</h3>
                                        <p>文章信息文章信息文章信息文章信章信息文章信章信息文章信章信息文章信章信息文章信息文章信息文章信息文章信息文章信息文章信息文章信息文章信息文章信息文章信文章信息文章信息文章信息文章信息</p>
                                    </div>
                                </div> -->
                                <router-link :to="{path:'/blogDetail',query:{blogId:item._id}}" tag="div" class="gestures">
                                    <div class="articleBox_title">
                                        <img :src="item.imgSrc" v-if="(item.imgSrc==''?false:true)">
                                        <div :class="{hasImgBox:(item.imgSrc==''?false:true)}">
                                            <h3>{{item.title}}</h3>
                                            <p>{{item.content.substring(0,100)}}...</p>
                                        </div>
                                    </div>
                                    <div class="articleBox_Bottom">
                                        <span><i class="el-icon-user"></i>{{item.author}}</span>
                                        <span><i class="el-icon-alarm-clock"></i>{{item.uptime.split('T')[0]}}</span>
                                        <span><i class="el-icon-view"></i>浏览（{{item.views}}）</span>
                                        <span><i class="el-icon-link"></i>{{item.tags}}</span>
                                    </div>
                                </router-link>
                                
                            </div>
                        </div>
                        
                        <div class="zanwu" v-else>
                            <img :src="zanwu">
                        </div>
                    </div>
                    
                    

                    <el-pagination
                    style="margin-top:20px"
                    background
                    layout="prev, pager, next"
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :total="totalNum">
                    </el-pagination>
  </div>
</template>

<script>

import axios from "../axios"
export default {
    props:['tagNameInfo'],
    data(){
        return{
            spanColor:'',
            spanSize:'',
            bannerList:[require('../assets/img/edf.jpg'),require('../assets/img/ert.jpg')],
            blogListData:[],
            totalNum:0,
            pageSize:5,
            pageIndex:1,
            tagName:'',
            loading:false,
            isData:true,
            bannerImgList:[],
        }
    },
    created(){
        console.log('70行',this.tagNameInfo)
        if(this.tagNameInfo){
            console.log('1111111111')
            this.getTagByIdInfo()
        }else{
            console.log('222222')
            this.pageBlogList();
        }
        // this.getTagByIdInfo();
        // console.log('333')
        this.zanwu=require('../assets/img/zanwu.jpg')
        this.getBannerImg();
        
    },
    mounted(){
        console.log(this.tagNameInfo)
    },
    methods:{
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            // console.log(`当前页: ${val}`,this.$route.query,val);
            this.pageIndex = val;
            this.pageBlogList();
            window.scrollTo(0,0)
        },
        //获取banner
        getBannerImg(){
            axios.get('/api/web/queryAllBannerIsTrue')
            .then(res=>{
                console.log(res)
                res.data.rows.forEach(item=>{
                    this.bannerImgList.push(item)
                })
            })
        },
        // 分页查询
        pageBlogList(){
            this.loading=true;
            axios.get('/api/web/queryPageBlog',{
                params:{
                    pageSize:this.pageSize,
                    pageIndex:this.pageIndex,
                    tags:this.tagName||''
                }
            })
                    .then(res=>{
                        if(res.data.status===1){
                            this.loading=false;
                            this.isData=true;
                            this.totalNum=res.data.total;
                            this.blogListData=res.data.bloglist;
                            this.blogListData.forEach((item,index)=>{
                                if(item.content.match(/<img[\w\W]*>/g)){
                                    var imgReg = /<img.*?(?:>|\/>)/gi;
                                    var srcReg = /src=[\'\"]?([^\'\"]*)[\'\"]?/i;
                                    var arr = item.content.match(imgReg);
                                    item.imgSrc=arr[0].match(srcReg)[1]
                                //     console.log('222',arr)
                                // console.log('333',arr[0].match(srcReg))
                                }else{
                                    item.imgSrc='';
                                }
                                
                                item.content=item.content.replace(/<img[\w\W]*>/g,"")
                                item.content=item.content.replace(/<[\w\W]{1,5}>/g, "");
                                item.content=item.content.replace(/<[\w\W]*>/g, "");
                                item.content=item.content.replace(/<\/[\w\W]*>/g, "");
                                item.content=item.content.substring(0,300);
                                console.log('11',item.content.match(/<[\w\W]{1,5}>/g))
                            })

                            this.totalNum=res.data.total;
                        }else if(res.data.status===10001){
                            this.isData=false;
                            this.loading=false;
                            this.totalNum=res.data.total;
                            this.blogListData=res.data.bloglist;
                            this.$message({
                                message: res.data.massage,
                                type: 'warning'
                            });
                        }
                        
                        // console.log(this.blogListData,res);
                    })
        },
        //增加浏览量
        addViewsNum(data){
            axios.post('/api/web/viewsNum',{
                id:data
            })
            .then(res=>{
                console.log(res)
            }).catch(err=>{
                console.log(err)
            })
        },
        // 按标签查询博客列表
        getTagBlogList(){
            axios.post('/api/web/tagsFind',{
                    tags:this.tagName
                }).then(res=>{
                    console.log('标线信息',res);
                    
                }).catch(err=>{
                    console.log(err);
                })
        },
        //获取标签信息
        getTagByIdInfo(){
        axios.post('/api/web/tagsByIdInfo',{
                tagId:this.tagNameInfo
            }).then(res=>{
                console.log(res);
                this.tagName=res.data[0].tag
                this.pageBlogList()
            }).catch(err=>{
                console.log(err);
            })
    }
        
        
    }
}
</script>

