<template>
  <div>
    <div class="whitebg lanmu">
      <img src="https://www.likeinlove.com/media/upfiles/1/d4f57957b3c09279d8d7acb761aed59228.png" />
      <h1>在这里~~~·留下你的足迹</h1>
      <p></p>
      <p>
        <span
          style="color: rgb(51, 51, 51); font-family: &quot;PingFang SC&quot;, &quot;Lantinghei SC&quot;, &quot;Microsoft YaHei&quot;, arial, 宋体, sans-serif, tahoma; background-color: rgb(255, 255, 255);"
        >夏日里花叶田田的荷塘,此时都只剩下了根根枯管,片片残叶.早已没了衔露含珠的风韵.寒风轻摇,枯和倓叶,仿佛悄悄的诉说着昔日美艳,又仿佛轻轻暗泣着如今没落.倘若再来一场冷雨,更催花落,倒符了李义山留得残荷听雨声的心境。</span>
      </p>
      <p>
        <span
          style="color: rgb(51, 51, 51); font-family: &quot;PingFang SC&quot;, &quot;Lantinghei SC&quot;, &quot;Microsoft YaHei&quot;, arial, 宋体, sans-serif, tahoma; background-color: rgb(255, 255, 255);"
        ></span>
      </p>
      <p></p>
    </div>

    <div class="fromMessage">
      <h4 class="messageTitle">
        <i class="el-icon-edit"></i>发表评论
        <span style="color:#666">（既然来了，就说点什么吧~）</span>
      </h4>
      <el-form ref="form" :model="form" label-width="0px">
        <el-form-item label class="opk">
          <el-input v-model="form.name" placeholder="请填写昵称"></el-input>
        </el-form-item>
        <el-form-item label class="opk">
          <el-input v-model="form.email" placeholder="邮箱（评论被回复时你能收到通知）"></el-input>
        </el-form-item>

        <el-form-item label>
          <el-input type="textarea" placeholder="无意义的内容我可能不会回复你" v-model="form.content"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="ReleaseComm">发布评论</el-button>
        </el-form-item>
      </el-form>
      <h4 class="messageTitle">
        <i class="el-icon-chat-dot-round"></i>评论列表
        <span style="color:#666">（本篇文章已有{{cont}}条评论）</span>
      </h4>
      <div id="blog_comments" class="blog_comments">
        <!-- <span class="blog_comments_msg">当前文章：1 条留言</span> -->
        <div class="blog_comment" v-for="(item) in commentsData" :key="item._id">
          <span class="span_comments">
            <b>{{item.user_name}}</b>
            ： 发表于 {{item.ctime}}
            <el-popover
            class="blog_comment_active"
                placement="bottom"
                
                width="500"
                trigger="click"
                content="这是一段内容,这是一段内容,这是一段内容,这是一段内容。">
                <el-form ref="form" :model="form" label-width="0px">
                    <el-form-item label class="opk">
                    <el-input v-model="form.name" placeholder="请填写昵称"></el-input>
                    </el-form-item>
                    <el-form-item label class="opk">
                    <el-input v-model="form.email" placeholder="邮箱（评论被回复时你能收到通知）"></el-input>
                    </el-form-item>

                    <el-form-item label>
                    <el-input type="textarea" placeholder="无意义的内容我可能不会回复你" v-model="form.content"></el-input>
                    </el-form-item>
                    <el-form-item>
                    <el-button type="primary" @click="ReleaseComm">发布评论</el-button>
                    </el-form-item>
                </el-form>
                <el-button slot="reference" size="mini" @click="ReplyComments({id:item._id,name:item.user_name,root_id:item._id})">回复</el-button>
            </el-popover>
          </span>
          <div>{{item.comments}}</div>
          <div class="ReplyBox" v-for="itempo in item.childre" :key="itempo._id">
             <div class="userReply">
                 <strong>{{itempo.user_name}}</strong><span>回复</span><strong>{{itempo.parentname}}：</strong>{{itempo.comments}}
             </div>
             <div class="userReplyButton">
                 <!-- <span>{{itempo.ctime}}</span><strong @click="ReplyComments({id:item._id,name:item.user_name})">回复2</strong>  -->
                 <span>{{itempo.ctime}}</span>
                 <el-popover
            class="blog_comment_active"
                placement="bottom"
                
                width="500"
                trigger="click"
                content="这是一段内容,这是一段内容,这是一段内容,这是一段内容。">
                <el-form ref="form" :model="form" label-width="0px">
                    <el-form-item label class="opk">
                    <el-input v-model="form.name" placeholder="请填写昵称"></el-input>
                    </el-form-item>
                    <el-form-item label class="opk">
                    <el-input v-model="form.email" placeholder="邮箱（评论被回复时你能收到通知）"></el-input>
                    </el-form-item>

                    <el-form-item label>
                    <el-input type="textarea" placeholder="无意义的内容我可能不会回复你" v-model="form.content"></el-input>
                    </el-form-item>
                    <el-form-item>
                    <el-button type="primary" @click="ReleaseComm">发布评论</el-button>
                    </el-form-item>
                </el-form>
                <el-button slot="reference" size="mini" @click="ReplyComments({id:itempo._id,name:itempo.user_name,root_id:item._id})">回复{{itempo._id}}</el-button>
            </el-popover>
             </div>
          </div>
        </div>
      </div>
    </div>
    <el-dialog title="请输入验证码" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
      <div class="codeBox">
        <span v-html="codeData.aa" @click="getCode">{{codeData.aa}}</span>
        <strong style="margin-right:10px">=</strong>
        <el-input v-model="inputCode" placeholder="请输入答案" maxlength='6' style="width:100px"></el-input>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmCode">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "../axios";
export default {
  data() {
    return {
      form: {
        name: "",
        email: "",
        content: ""
        //   date2: '',
        //   delivery: false,
        //   type: [],
        //   resource: '',
        //   desc: ''
      },
      blogId: "5dc3e18bbe44b299eca74a69",
      commentsData: [],
      codeData: "",
      dialogVisible: false,
      inputCode: "",
      cont:'',
      parent:'',
      parentname:'',
      root_id:'',
    };
  },
  created() {
    this.getComment();
  },
  methods: {
    ReleaseComm() {
      var email = this.form.email;
      var reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
      if (this.form.name === "") {
        this.$message.error("昵称不能为空");
        return;
      } else if (email === "") {
        this.$message.error("邮箱不能为空");
        return;
      } else if (this.form.content === "") {
        this.$message.error("评论内容不能为空");
        return;
      } else if (!reg.test(email)) {
        this.$message.error("邮箱格式错误");
        return;
      }
      this.getCode();
      this.dialogVisible = true;
    },
    confirmCode() {
      if (!this.inputCode) {
        this.$message.error("请填写图形码图形码错误");
      }
      if (this.codeData.text === this.inputCode) {
        axios
          .post("/api/web/addComment", {
            blog_id: this.blogId,
            // root_id:this.root_id,
            root_id:this.root_id,
            parent: this.parent,
            parentname: this.parentname,
            user_name: this.form.name,
            comments: this.form.content,
            email: this.form.email,
            ctime: "",
            uptime: ""
          })
          .then(res => {
            console.log("标线信息", res);
            if (res.data.status === 1) {
              this.$message({
                message: "恭喜你，评论成功",
                type: "success"
              });
            }
            this.getComment();
          })
          .catch(err => {
            console.log(err);
          });
        console.log(this.form);
        this.dialogVisible = false;
      } else {
        this.$message.error("图形码错误");
        this.getCode();
      }
    },
    ReplyComments(data){
        this.parent=data.id
        this.parentname=data.name
        this.root_id=data.root_id
    },
    getComment() {
      axios
        .post("/api/web/queryAllCommentByBlogId", {
          blogId: this.blogId
        })
        .then(res => {
          console.log("获取到信息了吗", res);
          this.commentsData = res.data.rows;
          this.cont = res.data.cont;
        })
        .catch(err => {
          console.log(err);
        });
      console.log(this.form);
    },
    getCode() {
        this.inputCode='';
      axios
        .post("/api/web/queryCode")
        .then(res => {
          console.log("验证码", res);
          this.codeData = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          console.log(done);
          done();
        })
        .catch(_ => {});
    }
  }
};
</script>

<style scoped>
.whitebg {
  background: #fff;
  border-radius: 3px;
  padding: 20px;
  margin-bottom: 10px;
  overflow: hidden;
}
.lanmu img {
  height: 100px;
  float: left;
  margin-right: 20px;
}
.lanmu h1 {
  font-size: 22px;
  margin-bottom: 15px;
}
.lanmu p {
  color: #666;
}
.lanmu p span {
  color: rgb(51, 51, 51);
  font-size: 15px;
  font-family: "PingFang SC", "Lantinghei SC", "Microsoft YaHei", arial, 宋体,
    sans-serif, tahoma;
  background-color: rgb(255, 255, 255);
}
.fromMessage {
  background: #fff;
  border-radius: 5px;
  padding: 20px 20px;
}
.opk {
  display: inline-block;
  width: 45%;
  margin-right: 5%;
}
.blog_comments {
  width: 100%;
  font-size: 14px;
}

.blog_comments_msg {
  margin-top: 0px;
  padding: 10px;
  display: inline-block;
  width: 100%;
  text-align: left;
}

.blog_comment {
  width: 95%;
  background: #f6f6f6;
  border: 1px dashed #ccc;
  padding: 10px 10px 20px 10px;
  margin-top: 10px;
  color: #31708f;
  text-align: left;
}

.blog_comment span.span_comments {
  display: inline-block;
  width: 100%;
  padding-left: 10px;
}

.blog_comment div {
  display: inline-block;
  width: 100%;
  padding-left: 10px;
  margin-top: 10px;
}
.messageTitle {
  font-weight: normal;
  font-size: 14px;
  border-bottom: 1px solid #398e9f;
  color: #398e9f;
  line-height: 40px;
  margin-bottom: 25px;
}
.messageTitle i {
  margin-right: 5px;
  font-size: 18px;
}
.el-textarea__inner {
  min-height: 100px !important;
}

.blog_comment div.userReply{
    padding:0;
}
.blog_comment div.userReply span{
    margin:0 5px;
}
.blog_comment div.ReplyBox{
    padding: 0 25px 0 25px;
    box-sizing: border-box;
}
.blog_comment div.userReplyButton{ display: flex; justify-content:space-between; padding: 0}
.blog_comment div.userReplyButton strong{ cursor: pointer;}
</style>