<template>
  <div>
      <div class="whitebg lanmu"><img src="../assets/img/ert.jpg" alt="Python缘起">
                    <h1>前端开发</h1>
                    <p>前端开发是创建Web页面或app等前端界面呈现给用户的过程，通过HTML，CSS及JavaScript以及衍生出来的各种技术、框架、解决方案，来实现互联网产品的用户界面交互。</p>
      </div>
      <div class="timebox">
          <div class="timeline">
              <ul v-if="isBlog">
                <div v-for="item in blogListArr" :key="item.year">
                   <h2>{{item.year}}</h2>
                          <li v-for="it in item.children" :key="it.title">
                            <h3>
                              {{it.day}}<span>{{it.month}}月</span>
                            </h3>
                            <router-link :to="{path:'/blogDetail',query:{blogId:it._id}}">{{it.title}}</router-link>
                            <!-- <a href="/programming/typescript-note-vue-to-ts.html" :title="it.title" target="_blank">{{it.title}}</a> -->
                        </li>
                </div>
                           
                            <!-- <h2>2018</h2>
                        
                        
                          <li>
                        
                            <h3>
                              04<span>11月</span>
                            </h3>
                            <a href="/programming/node-spider-async-maplimit-concurrency.html" title="Node爬虫之——使用async.mapLimit控制请求并发" target="_blank">Node爬虫之——使用async.mapLimit控制请求并发</a>
                        </li> -->
                          
                    
                
              </ul>
          </div>
    </div>
  </div>
</template>

<script>
import axios from "../axios";
export default {
    data() {
      return {
        reverse: true,
        blogTime:[],
        aa:[],
        blogListArr:[  ],
        isBlog:false,
      };
    },
    created(){
       console.log('11111',this.blogListArr)
      this.allblog();
       console.log('222222',this.blogListArr)
    },
    beforeMount(){
      console.log('8888',this.blogListArr)
    },
    mounted(){
      
      console.log('000000000000000',this.blogListArr)
    },
    methods:{
      // 获取所有博客
      allblog(){
        axios
        .get("/api/web/queryAllBlog")
        .then(res => {
          res.data.forEach(item => {
            let times = item.ctime.split('T')[0]
            this.blogTime.push({title:item.title,_id:item._id,year:times.split('-')[0],month:times.split('-')[1],day:times.split('-')[2],})
          });
          this.blogTime.forEach((item,index)=>{
            if(this.aa.indexOf(item.year)==-1){
               this.aa.push(item.year)
            }
          })
          this.aa.forEach((item,index)=>{
            this.blogListArr[index]={year:'',children:[]}
              this.blogTime.forEach(it=>{
                if(item==it.year){
                  this.blogListArr[index].year=item;
                  this.blogListArr[index].children.push(it)
                }
              })
            })
            this.isBlog=true;
          console.log('999999999999',this.blogTime,this.blogListArr)
        })
        .catch(err => {
          console.log(err);
        });
      },
    }

}
</script>

<style scoped>
